footer {
  background: var(--color-bg-primary);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
  color: #333;
}

[data-theme="dark"] footer {
  color: #fff;
}

/* Navigation Links */
.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}

.permalinks a {
  color: var(--color-white);
  transition: var(--transition);
}

.permalinks a:hover {
  color: var(--color-primary);
}

/* Social Links */
.footer__socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
}

.footer__socials a {
  background: var(--color-primary);
  color: #333;
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
  transition: var(--transition);
}

[data-theme="dark"] .footer__socials a {
  color: #fff;
}

.footer__socials a:hover {
  background: transparent;
  color: #666;
  border-color: var(--color-primary);
}

[data-theme="dark"] .footer__socials a:hover {
  color: #ccc;
}

/* Copyright */
.footer__copyright {
  margin-bottom: 4rem;
  color: #333;
}

[data-theme="dark"] .footer__copyright {
  color: #fff;
}

/* Theme-specific styles */
[data-theme="light"] .permalinks a {
  color: var(--color-bg);
}

[data-theme="light"] .permalinks a:hover {
  color: var(--color-primary);
}

[data-theme="light"] .footer__copyright {
  color: #333;
}

/* Media Queries */
@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    gap: 1.5rem;
  }

  .footer__socials {
    margin-bottom: 2.6rem;
  }
}
